import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { Navigate, Routes, Route } from 'react-router';
// import { withRouter } from 'react-router-native';
import { compose } from 'redux';
import { setAuth, setToken, setTokenV4Ac } from "../redux/auth-reducer";
import Cookie from 'universal-cookie';
import Preloader from "../components/Preloader";
const cookie = new Cookie();
const PermissionPage = ({token, accessToken, setAuth, setToken, setTokenV4Ac, accoundId, loginData}) => {
    useEffect(() => {
        if(!accessToken){
            // setAuth()
        }
        if(cookie.get('request_token')){
            // setTokenV4Ac(cookie.get('request_token'))
            setToken(cookie.get('request_token'))
        }
    }, [])
    const getTokenHandler=()=>{
        setToken(token)
    }
    if(!cookie.get('request_token') || (loginData)) return <Routes><Route render={() => <Navigate to="/" />} /></Routes>
        return (   
            <section>
                <div className="container-fluid">
                    {/* <button className="btn" onClick={getTokenHandler}>Get Tokens</button> */}
                    <div className="modal active" 
                    // style={{backgroundImage: "url(https://assets.nflxext.com/ffe/siteui/vlv3/03fdc4bf-72f6-4926-83a7-a76e6a1a5591/0bd2016b-9d45-4e2c-bd56-2a25992864c0/BY-ru-20211115-popsignuptwoweeks-perspective_alpha_website_medium.jpg)"}}
                    >
                        <div className="modal-content">
                            <div className="box">
                                <h3 className="title small center">Идет авторизация...</h3>
                                {(loginData && <p>Готово!</p>) ?? <Preloader className="preloader_static"/> }                        
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
};
const mapStateToProps = (state) => {
    return {
        token: state.auth.tokenV4,
        accoundId: state.auth.accountId,
        accessToken: state.auth.accessToken,
        loginData: state.auth.loginData,
    }
}
export default compose(
    connect(mapStateToProps, { setAuth, setToken, setTokenV4Ac })
)(PermissionPage)